import Image from "next/image";

export default function WomenCategoryPage({ products }) {
  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold text-center mb-8">Women's Category</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {products.map((product) => (
          <ProductCard key={product.id} product={product} />
        ))}
      </div>
    </div>
  );
}

function ProductCard({ product }) {
  return (
    <div className="p-4 border rounded-lg shadow-lg cursor-pointer hover:shadow-xl transition">
      <Image 
        src={product.image} 
        alt={product.title} 
        width={300} 
        height={200} 
        className="w-full h-40 object-cover rounded-lg"
        unoptimized
      />
      <p className="text-center font-semibold mt-2">{product.title}</p>
    </div>
  );
}

// ðŸ”¥ Server-Side Fetching (SSR)
export async function getServerSideProps() {
  try {
    const response = await fetch("https://fakestoreapiserver.reactbd.com/products");
    const products = await response.json();

    return {
      props: { products }, // Pass data as props to the component
    };
  } catch (error) {
    console.error("Error fetching products:", error);
    return {
      props: { products: [] }, // Return an empty array if there's an error
    };
  }
}
